.make-page-container{
    display:flex;
    flex-direction:row;
    height: 100%;
    width:100%;
}

.make-page-example{
    width:70%;
    height:100%;
    background-color:pink;
}
.make-page-make-space{
    position:fixed;
    right:0;
    width:30%;
    height:100%;
    background-color:white;
    flex-direction: column;
    display: flex;
    box-shadow: 0 0 17px rgba(0,0,0,0.2);
}

.colorSelection{
    margin:10px;
    display: flex;
    justify-content : center;
    align-items :center;
    width:90px;
    height:90px;
    background-color:white;
    border-radius: 10px;
    box-shadow: 3px 3px 6px rgba(0,0,0,0.4);
}

.fontSelection{
    margin:10px;
    display: flex;
    justify-content : center;
    align-items :center;
    width:100%;
    height:50px;
    background-color:white;
    border-radius: 10px;
    box-shadow: 3px 3px 6px rgba(0,0,0,0.4);
    cursor:pointer;
}

.section-one-make-one{
    display: flex;
    justify-content :center;
    flex-direction:column;
    width:100%;
}

.section-table-container{
    justify-content: start;
    width:80%;
    padding-left: 20px; 
}

.section-selector-container{
    display: flex;
    flex-direction:row;
}

.make-page-section-selector{
    height:40px;
    width:100%;
    border-top:1px solid black;
    border-left:1px solid black;
    border-right:1px solid black;
    background-color:white;
    cursor:pointer;
    display: flex;
    justify-content :center;
    align-items :center;
}

.section-image{
    width:200px;
    height:200px;
}

.section-one-make{
    display: flex;
    flex-direction: column;
}
.section-one-title-input{
    width:70%;
    font-size: 19px;
}

.file-input{
    width:30%;
}
.col-image{
    width:300px;
}