.nav-bar-container{
    position: fixed;
    top: 0;
    z-index:3;
    height:80px;
    background-color:white;
    width: 100%;
    display: flex;
    justify-content: start;
    align-items : center;
    padding-left:5%;
    border-bottom: 1px solid black;
}

.nav-bar-title{
    color:#6B63F7;
    font-size:50px;
    width:50%;
    font-weight: 700;
}

.nav-on{
    display: flex;
    align-items :center;
    width:50%;
    cursor:pointer;
}


.nav-on:hover{
    font-weight:700;
}