.main-page-container{
    height:100%;
    width:100%;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: 'OTWelcomeBA';
}

.main-page-section{
    width:100%;
    display: flex;
    flex-direction: row;
    height:600px;
    justify-content : center;
    align-items : center;
}

.main-section-left{
    width:40%;
    height: 100%;
    display:flex;
    justify-content : center;
    align-items : start;
    flex-direction: column;
    padding-left:9%;
}
.main-section-right{
    width:35%;
    height: 100%;
    display:flex;
    justify-content : center;
    align-items : center;
}

.middle-section{
    width:100%;
    display:flex;
    justify-content : center;
    align-items : start;
    flex-direction: column;
}

.one-section-big{
    text-align: start;
    font-size:50px;
    font-weight: 500;
}

.one-section-small{
    margin-top:7%;
    text-align: start;
    font-size:18px;
    font-weight: 400;
}

.apply-button{
    margin-top:7%;
    cursor: pointer;
    border: 5px solid white;
    background-color:white;
    border-radius:50px;
    color:#6a327f;
    width:22vw;
    height:110px;
    display: flex;
    align-items:center;
    justify-content : center;
    font-size: 1.4vw;
    font-weight: 400;
    text-decoration: none;
}

.apply-button:hover{
    background-color:white;
    color:black;
}

.main-page-section-last{
    width:100%;
    display: flex;
    flex-direction: row;
    height:500px;
    justify-content : center;
    align-items : center;
}

.main-section-last-title{
    width:100%;
    display: flex;
    flex-direction: row;
    justify-content : center;
    align-items : center;
    font-size:3vw;
    color:white;
    font-weight:500;
}

.main-section-last-desc{
    margin-top:7%;
    width:100%;
    display: flex;
    flex-direction: row;
    justify-content : center;
    align-items : center;
    font-size:1.4vw;
    color:white;
    font-weight:400;
}

.last-one-component{
    width:40%;
    display: flex;
    flex-direction: column;
    height:700px;
    justify-content : center;
    align-items : center;
}

@media screen and (max-width:800px){
    .main-page-container{
        height:100%;
        width:100%;
        display:flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        font-family: 'ImcreSoojin';
    }
    
    .main-page-section{
        width:100%;
        display: flex;
        flex-direction: column;
        height:800px;
        justify-content : center;
        align-items : center;
    }
}